import React from 'react';

import { Link } from 'react-router';

const ListWorkoutsList = ({workouts}) => {
    if(workouts === undefined)
        return <p>No workouts yet! How about you create one?</p>

    var wo = Object.keys(workouts);
    
    return (
        <ul className="mdl-list">
            { wo.map((key) => {
                var w = workouts[key];
                return (
                    <li className="mdl-list__item mdl-list__item--three-line" key={key}>
                        
                        <span className="mdl-list__item-primary-content">
                            { /* TODO Icon by type <i className="material-icons mdl-list__item-avatar">person</i> */ }
                            <span><Link to={`/workouts/${key}`}>{w.info.title}</Link></span>
                            <span className="mdl-list__item-text-body">
                                {w.info.notes}
                            </span>
                        </span>

                        <span className="mdl-list__item-secondary-content">
                           <b>{w.info.distance}</b>
                        </span>
                    </li>
                );
            })}
        </ul>
    )
}

export default ListWorkoutsList;


