import React from 'react';

const ListWorkoutsFilter = ({tags, selected, filterHandler}) => {
    if(tags === undefined)
        return <p>No tags yet!</p>

    return ( 
        <div className="mdl-grid">
            <div className="mdl-cell mdl-cell--8-col">
                <h4>What type of workout are you looking for?</h4>
        
                    { tags.map((key) => {
                        var classname = (key.includes(selected)) ? "mdl-button mdl-js-button mdl-button--accent" : "mdl-button mdl-js-button";
                        return (
                            <button className={classname} key={key} onClick={ () => filterHandler(key) }>{key}</button> 
                        );
                    })}
            
            </div>

        </div>
    )
}

export default ListWorkoutsFilter;






